@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
input {
  padding: 6px;
  text-align: center;
  font-size: 1.5rem;
  outline: none;
  font-family: "Roboto Mono", monospace;
  border-radius: 15px;
  border: 1px solid black;
  margin-bottom: 1rem;
}

button {
  font-family: "Roboto Mono", monospace;
  border-radius: 15px;
  border: 1px solid black;
  padding: 10px;
  font-size: 0.9rem;
  font-weight: 700;
  background-color: lightpink;
  color: #000;
  outline: none;
}

h2 {
  color: #fff;
}

img {
  border: 3px solid #fff;
}

body {
  font-family: "Roboto Mono", monospace;
}

.wrapper {
  max-width: 900px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #222;
  min-height: 98.5vh;
  border-radius: 15px;
}

.title {
  text-align: center;
}

.title h1 {
  background: -webkit-linear-gradient(rgb(255, 255, 255), rgb(181, 109, 196));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.title h4 {
  color: white;
  font-weight: 200;
}

.content h3 {
  color: #fff;
}

.content {
  text-align: center;
}

